
(function sc_add_charts() {

  var ctx = document.getElementById('socialChartInstagram').getContext('2d');
  var chart = new Chart(ctx, {
    // The type of chart we want to create
    type: 'line',
  
    // The data for our dataset
    data: {
      
      // labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
      datasets: [{
        label: sc_chart_data.username + ' - Instagram Follower Count',
        backgroundColor: 'rgb(255, 99, 132)',
        borderColor: 'rgb(255, 99, 132)',
        data: sc_chart_data.data,
        fill: false,
        // borderColor: 'rgb(83, 142, 240)',
        // backgroundColor: 'rgb(83, 142, 240)',
      }]
    },
  
    // Configuration options go here
    options: {
      scales: {
          xAxes: [{
              type: 'time',
              time: {
                  source: 'data',
                  parser: 'YYYY-MM-DD',
                  bounds: 'ticks'
              }
          }],
          yAxes: [{
            ticks: {
                // Include a dollar sign in the ticks
                callback: function(value, index, values) {
                    return parseInt(value);
                }
            }
        }]
      }
  }
  });

})();
